.page {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

.button {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate3d(-50%, -50%, 0);
            transform: translate3d(-50%, -50%, 0);
    background-color: #36b34a;
    color: #fff;
    font-size: 50px;
    line-height: 1em;
    font-weight: bold;
    padding: 50px;
    min-width: 150px;
    text-transform: uppercase;
    border-radius: 100px;
    cursor: pointer;
}

.button.play {
    padding-left: 170px;
}

.button > span {
    position: relative;
}

.button.play:before {
    content: '';
    display: block;
    position: absolute;
    top: calc(50% - 50px);
    left: 60px;
    border-left: 80px solid white;
    border-top: 50px solid transparent;
    border-bottom: 50px solid transparent;
}

.button:before,
.button > span {
    z-index: 2;
}

.button:after {
    content: '';
    display: block;
    position: absolute;
    background-color: #36b34a;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    border-radius: 200px;
    z-index: 1;
    -webkit-animation: button_animation 2s ease infinite;
            animation: button_animation 2s ease infinite;
}

@-webkit-keyframes button_animation {
    from {
        -webkit-transform: translate3d(0, 0, 0);
                transform: translate3d(0, 0, 0);
        width: 100%;
        height: 100%;
        opacity: .7;
    }
    to {
        -webkit-transform: translate3d(-50px, -50px, 0);
                transform: translate3d(-50px, -50px, 0);
        width: calc(100% + 100px);
        height: calc(100% + 100px);
        opacity: 0;
    }
}

@keyframes button_animation {
    from {
        -webkit-transform: translate3d(0, 0, 0);
                transform: translate3d(0, 0, 0);
        width: 100%;
        height: 100%;
        opacity: .7;
    }
    to {
        -webkit-transform: translate3d(-50px, -50px, 0);
                transform: translate3d(-50px, -50px, 0);
        width: calc(100% + 100px);
        height: calc(100% + 100px);
        opacity: 0;
    }
}